<template>
  <div class="pushmore-index index-er">
    <div class="head">
      <img :src="headimgurl != '' ? headimgurl : headless" />
      <div class="head-text" v-if="user != ''">
        <p>
          <label></label>
          <span>{{ nickname }}</span>
        </p>
        <p>
          <label>ID</label>
          <span>{{ tddCode }}</span>
        </p>
        <p>
          <label></label>
          <span>{{ parentNick != null ? parentNick : "" }}</span>
        </p>
      </div>
      <h3 v-else></h3>
    </div>

    <div class="menu-list">
      <div class="list" @click="myTeam">
        <i class="iconfont icon-tuandui icon"></i>
        <span class="middle-text"></span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
      <div class="list" @click="myQrCode">
        <i class="iconfont icon-two-dimensional-code icon icon-middle"></i>
        <i class="iconfont icon-AppIcon-copy card-circle"></i>
        <i class="iconfont icon-shouzhixuanzhong-copy card-finger"></i>
        <span class="middle-text">
          <span class="card"></span>
          <span v-show="remainingTime" class="remaining-time"
            >(...)<i
              class="iconfont icon-dian-red remaining-icon"
            ></i
          ></span>
        </span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
      <div class="list none-border" @click="myReward">
        <i class="iconfont icon-jiangli icon icon-right"></i>
        <span class="middle-text"></span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
    </div>

    <div class="reminder">
      <i class="iconfont icon-jinggao reminder-icon"></i>
      <span></span>
    </div>

    <div class="activity-rules-desc" id="rules">
      <h3></h3>
      <p>
        1
      </p>
      <p>
        2
      </p>
      <p>
        3
      </p>
      <p>
        440
      </p>
      <p>54008792099</p>
    </div>

    <van-overlay :show="show">
      <div class="show">
        <h3></h3>
        <div class="field">
          <van-field
            v-model="licenese"
            type="text"
            clearable
            label=""
            placeholder=""
          />
        </div>
        <div class="show-but">
          <button class="but" @click="clickClose"></button>
          <button class="but" @click="submitBindRecomend" :disabled="isDisable">
            
          </button>
        </div>
      </div>
    </van-overlay>

    <van-overlay :show="show1" @click="show = false" class="overlay">
      <div class="wrapper">
        <h3>!</h3>
        <p class="p1"></p>
        <p class="p2" @click="myReward"> </p>
      </div>
      <van-icon name="clear" class="clear" @click="clickClear" />
    </van-overlay>

    <van-dialog
      v-model="show2"
      title=""
      cancel-button-text=""
      confirm-button-text=""
      show-cancel-button
      class="dialog"
      @cancel="clickClose"
    >
      <p class="con">30</p>
    </van-dialog>
  </div>
</template>

<script>
import AeraInfo from "../../utils/area";
import {
  userInfo,
  bindRecomend,
  myCard,
  updateMyCard,
  myUnreceiveAaward,
} from "../../api/api";
import wx from "weixin-js-sdk";
export default {
  name: "IndexInfo",
  data() {
    return {
      user: "",
      show: false,
      show1: false,
      show2: false,
      headimgurl: "",
      nickname: "",
      tddCode: "",
      parentNick: "",
      headless: require("../../assets/img/Headless.png"),
      licenese: "",
      url: "",
      expiredTime: "",
      remainingTime: false,
      timeExpired: 24 * 60 * 60, // 
      isDisable: false,
    };
  },
  mounted() {
    let that = this;
    that.getUserInfo();
    that.$getRequest(myCard).then((res) => {
      if (res.data.code === 0) {
        that.url = res.data.url;
        that.expiredTime = res.data.expiredTime;
        if (that.expiredTime < that.timeExpired) {
          that.remainingTime = true;
        } else {
          that.remainingTime = false;
        }
      }
    });
    that.$getRequest(myUnreceiveAaward).then((res) => {
      if (res.data.code === 0) {
        if (res.data.countNum > 0) {
          that.show1 = true;
        } else {
          that.show1 = false;
        }
      }
    });
  },
  methods: {
    getUserInfo() {
      let that = this;
      that.$getRequest(userInfo).then((res) => {
        if (res.data.code === 0) {
          that.user = res.data.user;
          let {
            headimgurl,
            nickname,
            parentNick,
            tddCode,
            isbind,
            recommendUrl,
            recommendUrlTime,
            isable,
          } = res.data.user;
          that.headimgurl = headimgurl;
          that.nickname = nickname;
          that.tddCode = tddCode;
          that.parentNick = parentNick;
          if (isbind === "0") {
            that.show = true;
          } else {
            that.show = false;
          }
          if (isable === 0) {
            that.show2 = true;
          } else {
            that.show2 = false;
          }
        } else {
        }
      });
    },
    // 
    submitBindRecomend() {
      let that = this;
      if (
        that.licenese === "" ||
        typeof that.licenese === undefined ||
        that.licenese === null
      ) {
        that.$toast("");
        return;
      }
      let params = {
        licenese: that.licenese,
      };
      that.isDisable = true;
      that.$postRequest(bindRecomend, params).then((res) => {
        setTimeout(() => {
          that.isDisable = false;
        }, 2000);
        if (res.data.code === 0) {
          that.$toast.success("");
          that.show = false;
          that.licenese = "";
          that.getUserInfo();
        } else {
          that.$toast.fail(res.data.msg);
        }
      });
    },
    clickClear() {
      this.show1 = false;
    },
    //
    clickClose() {
      wx.closeWindow();
    },
    // 
    myReward() {
      this.$router.push({ path: "/MyReward" });
    },
    // 
    myTeam() {
      this.$router.push({
        path: "/MyTeam",
      });
    },
    // 
    myQrCode() {
      let that = this;
      if (that.expiredTime < that.timeExpired) {
        that.$postRequest(updateMyCard).then((res) => {
          if (res.data.code === 0) {
            that.$toast.success("");
            that.$router.push({ path: "/MyQrCode", query: { url: that.url } });
          } else {
            that.$toast.fail(res.data.message);
          }
        });
      } else {
        that.$router.push({
          path: "/MyQrCode",
          query: { url: that.url },
        });
      }
    },
  },
  beforeDestroy() {},
};
</script>

<style >
</style>


